'use client';
import React, { useState } from "react";

type TableProps = {
  columns: { key: string; label: string }[];
  data: Record<string, React.ReactNode>[];
  selectable?: boolean;
  pageSize?: number;
};

const PAGE_SIZE_OPTIONS = [10, 20, 50, 100];

export default function Table({ columns, data,  pageSize = 10 }: TableProps) {
  const [page, setPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(pageSize);

  const totalPages = Math.ceil(data.length / rowsPerPage);
  const paginatedData = data.slice((page - 1) * rowsPerPage, page * rowsPerPage);

  // Helper for pagination numbers (simple version)
  const getPages = () => {
    const pages = [];
    if (totalPages <= 5) {
      for (let i = 1; i <= totalPages; i++) pages.push(i);
    } else {
      if (page <= 3) {
        pages.push(1, 2, 3, 4, 5);
      } else if (page >= totalPages - 2) {
        for (let i = totalPages - 4; i <= totalPages; i++) pages.push(i);
      } else {
        for (let i = page - 2; i <= page + 2; i++) pages.push(i);
      }
    }
    return pages;
  };

  // Reset page if rowsPerPage changes
  React.useEffect(() => {
    setPage(1);
  }, [rowsPerPage]);

  return (
    <div className="relative overflow-x-auto shadow-md sm:rounded-lg">
      <table className="w-full text-sm text-left bg-surface-light dark:bg-surface-dark border border-border-light dark:border-border-dark">
        <thead className="text-xs uppercase bg-background-light dark:bg-background-dark text-text-light dark:text-muted-dark border-b border-border-light dark:border-border-dark">
          <tr>
            {columns.map((col) => (
              <th key={col.key} scope="col" className="px-6 py-3 font-semibold text-center">
                {col.label}
              </th>
            ))}
          </tr>
        </thead>
        <tbody>
          {paginatedData.map((row, i) => {
            const globalIndex = (page - 1) * rowsPerPage + i;
            return (
              <tr
                key={i}
                className={
                  "text-text-light dark:text-text-dark border-b border-border-light dark:border-border-dark hover:bg-background-light dark:hover:bg-background-dark transition-colors " +
                  ((globalIndex % 12 === 0)
                    ? "bg-primary !text-text-dark"
                    : (i % 2 === 1
                        ? "text-muted-light dark:text-text-muted-dark bg-background-light dark:bg-background-dark"
                        : "text-muted-light dark:text-text-muted-dark"))
                }
              >
                {columns.map((col, j) =>
                  j === 0 ? (
                    <th
                      key={col.key}
                      scope="row"
                      className="px-6 py-2 font-medium whitespace-nowrap text-center"
                    >
                      {row[col.key]}
                    </th>
                  ) : (
                    <td key={col.key} className="px-6 py-2 text-center">
                      {row[col.key]}
                    </td>
                  )
                )}
              </tr>
            );
          })}
        </tbody>
      </table>
      {/* PAGINATION */}
      <nav
        className="w-full flex flex-col items-start justify-between border-x border-b border-border-light dark:border-border-dark p-4 space-y-3 md:flex-row md:items-center md:space-y-0"
        aria-label="Table navigation"
      >
        <div className="flex items-center gap-4 whitespace-nowrap">
          <label className="ml-2 font-semibold text-xs text-muted-light dark:text-muted-dark flex items-center gap-1">
            Rows per page:
            <select
              className="ml-1 w-20 rounded border border-border-light dark:border-border-dark bg-surface-light dark:bg-surface-dark text-xs text-text-light dark:text-text-dark px-2 py-1"
              value={rowsPerPage}
              onChange={e => setRowsPerPage(Number(e.target.value))}
            >
              {PAGE_SIZE_OPTIONS.map(opt => (
                <option key={opt} value={opt}>{opt}</option>
              ))}
            </select>
          </label>
          <span className="text-sm font-normal text-muted-light dark:text-muted-dark">
            <span className="font-semibold text-text-light dark:text-text-dark">
              {(page - 1) * rowsPerPage + 1}-{Math.min(page * rowsPerPage, data.length)}
            </span>{" "}
            of{" "}
            <span className="font-semibold text-text-light dark:text-text-dark">
              {data.length}
            </span>
          </span>
        </div>
        <ul className="inline-flex -space-x-px text-sm rounded-lg overflow-hidden border border-border-light dark:border-border-dark">
          {/* Botón ir al principio */}
          <li>
            <button
              onClick={() => setPage(1)}
              disabled={page === 1}
              className={`flex items-center justify-center px-3 h-8 ms-0 leading-tight
                text-muted-light dark:text-muted-dark
                !bg-surface-light !dark:bg-surface-dark
                border border-e-0 border-border-light dark:border-border-dark
                rounded-s-lg
                hover:bg-background-light hover:text-text-light
                dark:hover:bg-background-dark dark:hover:text-text-dark
                ${page === 1 ? "opacity-50 cursor-not-allowed" : ""}`}
              aria-label="Primera página"
            >
              <span className="sr-only">Primera</span>
              {/* Doble flecha izquierda */}
              <svg className="w-4 h-4" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" d="M15 19l-7-7 7-7" />
                <path strokeLinecap="round" strokeLinejoin="round" d="M19 19l-7-7 7-7" />
              </svg>
            </button>
          </li>
          {/* Botón anterior */}
          <li>
            <button
              onClick={() => setPage((p) => Math.max(1, p - 1))}
              disabled={page === 1}
              className={`flex items-center justify-center px-3 h-8 leading-tight
                text-muted-light dark:text-muted-dark
                bg-surface-light dark:bg-surface-dark
                border border-border-light dark:border-border-dark
                hover:bg-background-light hover:text-text-light
                dark:hover:bg-background-dark dark:hover:text-text-dark
                ${page === 1 ? "opacity-50 cursor-not-allowed" : ""}`}
              aria-label="Anterior"
            >
              <span className="sr-only">Anterior</span>
              <svg className="w-4 h-4" aria-hidden="true" fill="currentColor" viewBox="0 0 20 20">
                <path
                  fillRule="evenodd"
                  d="M12.707 5.293a1 1 0 010 1.414L9.414 10l3.293 3.293a1 1 0 01-1.414 1.414l-4-4a1 1 0 010-1.414l4-4a1 1 0 011.414 0z"
                  clipRule="evenodd"
                ></path>
              </svg>
            </button>
          </li>
          {/* Botones de página */}
          {getPages().map((p, idx) => (
            <li key={idx}>
              <button
                onClick={() => setPage(Number(p))}
                aria-current={page === p ? "page" : undefined}
                className={`flex items-center justify-center px-3 h-8 leading-tight border border-border-light dark:border-border-dark
                  ${
                    page === p
                      ? "text-text-light border-primary font-bold"
                      : "text-muted-light dark:text-muted-dark bg-surface-light dark:bg-surface-dark hover:bg-background-light hover:text-text-light dark:hover:bg-background-dark dark:hover:text-text-dark"
                  }`}
              >
                {p}
              </button>
            </li>
          ))}
          {/* Botón siguiente */}
          <li>
            <button
              onClick={() => setPage((p) => Math.min(totalPages, p + 1))}
              disabled={page === totalPages}
              className={`flex items-center justify-center px-3 h-8 leading-tight
                text-muted-light dark:text-muted-dark
                bg-surface-light dark:bg-surface-dark
                border border-border-light dark:border-border-dark
                hover:bg-background-light hover:text-text-light
                dark:hover:bg-background-dark dark:hover:text-text-dark
                ${page === totalPages ? "opacity-50 cursor-not-allowed" : ""}`}
              aria-label="Siguiente"
            >
              <span className="sr-only">Siguiente</span>
              <svg className="w-4 h-4" aria-hidden="true" fill="currentColor" viewBox="0 0 20 20">
                <path
                  fillRule="evenodd"
                  d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z"
                  clipRule="evenodd"
                ></path>
              </svg>
            </button>
          </li>
          {/* Botón ir al final */}
          <li>
            <button
              onClick={() => setPage(totalPages)}
              disabled={page === totalPages}
              className={`flex items-center justify-center px-3 h-8 leading-tight
                text-muted-light dark:text-muted-dark
                bg-surface-light dark:bg-surface-dark
                border border-s-0 border-border-light dark:border-border-dark
                rounded-e-lg
                hover:bg-background-light hover:text-text-light
                dark:hover:bg-background-dark dark:hover:text-text-dark
                ${page === totalPages ? "opacity-50 cursor-not-allowed" : ""}`}
              aria-label="Última página"
            >
              <span className="sr-only">Última</span>
              {/* Doble flecha derecha */}
              <svg className="w-4 h-4" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" d="M9 5l7 7-7 7" />
                <path strokeLinecap="round" strokeLinejoin="round" d="M5 5l7 7-7 7" />
              </svg>
            </button>
          </li>
        </ul>
      </nav>
    </div>
  );
}